.contact-request-form-panel {
  margin-top: 1rem; }
  .contact-request-form-panel .book-now-button span.inline-block {
    margin-left: 0.5rem; }
  .contact-request-form-panel .contact-request-form {
    padding-top: 1rem;
    border: 1px solid #BABCBE;
    background-color: #EAEAEA;
    box-shadow: 0 0 2px rgba(160, 160, 160, 0.2); }
  .contact-request-form-panel .contact-form .error small.error {
    display: inline; }
  .contact-request-form-panel .contact-input {
    font-weight: 400;
    transition-property: border-color, box-shadow !important;
    margin-bottom: 1rem; }
  .contact-request-form-panel .submit-button {
    display: block;
    height: 2.3125rem;
    padding: 0;
    letter-spacing: 1px;
    max-width: 240px; }
  .contact-request-form-panel #form-sent-feedback {
    display: none; }

.itinerary-description {
  margin-bottom: 0; }

.riding-distance {
  display: inline-block;
  background-color: rgba(148, 192, 68, 0.5);
  border: 1px solid #94C044;
  padding: 0.25rem 0.5rem;
  margin-right: 0.5rem;
  margin-bottom: 1.25rem; }

.things-needed {
  margin: 0; }
  .things-needed .icon-big {
    width: 2rem;
    height: 2rem;
    float: left; }
  .things-needed li {
    list-style: none; }
    .things-needed li .feature-item {
      margin-left: 2.5rem;
      font-size: 1.125rem; }
